html,
body {
  height: 100%;
}

body {
  font-family: Helvetica, sans-serif;

  margin: 0;
  padding: 0;

  background: linear-gradient(
    180deg,
    rgba(9, 35, 175, 1) 0%,
    rgba(0, 0, 0, 1) 100%
  );
  color: #fff;
}


button {
  background: #1646a0;
  background-repeat: no-repeat;
  color: #fff;

  padding:  0px 45px;
  border:  2px solid #FFF;
  border-radius: 25px;
  height: 50px;

  text-transform: uppercase;

  font-weight: bold;
  font-size:  1.2em;

  cursor: pointer;
  transition: .4s;
}

button:hover {
  background-color: #0923af;
}